import cn from "classnames";
import styles from "./ButtonLogin.module.scss";

const ButtonLogin = ({ name, mode }) => {
  return (
    <button
      type="button"
      className={cn(styles.btnReset, {
        [styles.btnLogin]: mode === "login",
        [styles.btnRegistration]: mode === "registration",
      })}
    >
      {name}
    </button>
  );
};

export { ButtonLogin };
