import { ModalAndWrapper } from "./ModalAndWrapper";
import { InputLogin } from "../../InputLogin";
import { TitleLogin } from "../../TitleLogin";
import { TextLogin } from "../../TextLogin";
import { ButtonLogin } from "../../ButtonLogin/ButtonLogin";

const Login = () => {
  return (
    <ModalAndWrapper>
      <TitleLogin title="Корпоративная сеть" />
      <InputLogin mode="inputBorder" label="Почта" type="email" />
      <TextLogin text="Забыли пароль?" posLeft="right" />
      <InputLogin mode="inputBorder" label="Пароль" type="password" />
      <TextLogin text="Нет учетной записи?" linked=" Создать." />
      <ButtonLogin mode="login" name="Войти" />
    </ModalAndWrapper>
  );
};

export { Login };
