import { ButtonLogin } from "../../ButtonLogin/ButtonLogin";
import { InputLogin } from "../../InputLogin";
import { TextLogin } from "../../TextLogin";
import { ModalAndWrapper } from "./ModalAndWrapper";

const RegistrationOne = () => {
  return (
    <ModalAndWrapper>
      <TextLogin text="1 / 3" />
      <InputLogin mode="inputLine" label="Имя" type="text" />
      <InputLogin mode="inputLine" label="Фамилия" type="text" />
      <div
        style={{
          width: "100%",
          display: "flex",
          "justify-content": "space-around",
        }}
      >
        <ButtonLogin mode="registration" name="Назад" />
        <ButtonLogin mode="registration" name="Далее" />
      </div>
    </ModalAndWrapper>
  );
};

export { RegistrationOne };
